import React from "react";
import {
	HashRouter as Router,
	Switch,
	Route,
	Redirect,
} from "react-router-dom";

import '../styles/Styles.scss';
import { AboutScreen } from "../components/about/AboutScreen";
import { ContactScreen } from "../components/contact/ContactScreen";
import { HomeScreen } from "../components/home/HomeScreen";
import { NavApp } from "../components/nav/NavApp";
import { SkillsApp } from "./SkillsApp";

export const AppRouter = () => {

	return (
		<div className="body d-flex align-items-center flex-column">
			<Router basename="/portfolio">
				<NavApp />
				<figure className="body__line bg-lightgreen animate__animated animate__zoomIn animate__slower" />
				<main className="body__window-container align-items-center justify-content-center animate__animated animate__slideInUp animate__slow">
					<div className="body__window-box d-flex flex-row align-items-center justify-content-center border-lightgreen">
						<div className="w-100 h-100 d-flex align-items-center justify-content-center">
							<Switch>
								<Route exact path="/" component={HomeScreen} />
								<Route path="/about" component={AboutScreen} />
								<Route path="/skills" component={SkillsApp} />
								<Route path="/contact" component={ContactScreen} />
								<Redirect to="/" />
							</Switch>
						</div>
						<aside className="body__solid-box" id="solid">
							<div className="body__solid-box-line" id="line" />
						</aside>
					</div>
				</main>
			</Router>
		</div>
	);
}