import React from 'react';

export const KnowledgeApp = ({ tech }) => {

    const { technology, iconTechnology,range } = tech;

    return (
        <div className="w-100 h-100  row mt-4">
            <i className={ `${iconTechnology} ${technology}-knowledge text-knowledge col-12 d-flex align-items-center justify-content-center animate__animated animate__fadeInUp` } />
            <div className="col-12 mt-5 w-100 d-flex align-items-center justify-content-center flex-column animate__animated animate__fadeInDown">
                <div className="progress w-50">
                    <div className={ `progress-bar ${technology}-on` } role="progressbar" style={{ width: `${range}%`}} aria-valuenow="25" aria-valuemin="0" aria-valuemax="100"></div>
                </div>
            </div>
        </div>
    );
};
    