import React from 'react'
import { data } from '../../data/data-knowledge';
import { KnowledgeApp } from './KnowledgeApp';

export const SkillsScreen = () => {
    return (
        <div className="w-100 h-100">
            <div className = "skills__title-cointeiner w-100 d-flex align-items-center justify-content-center flex-column">
                <h1 className = "skills__title animate__animated animate__fadeInDown">Skills</h1>
                <div className = "skills__title-line animate__animated animate__zoomIn"/>
            </div>
            <div className = "skills__icons-container w-100 d-flex justify-content-evenly animate__animated animate__fadeInUp">
                <div id="carouselExampleIndicators" className="carousel slide caruselo-test" data-bs-ride="carousel">
                    <div className="carousel-indicators">
                        {data.map(( db, i )=>{
                            return (                        
                                <button 
                                 type="button" 
                                 data-bs-target="#carouselExampleIndicators" 
                                 data-bs-slide-to={ `${i}` } 
                                 className={ `${db.active}` }
                                 key={`${i}`}
                                 aria-current="true" 
                                 aria-label={ `Slide ${i}` }
                                />
                        )})}
                    </div>
                    <div className="carousel-inner caruselo-test">
                        { data.map( tech => {
                            return (
                                <div className={ `carousel-item ${tech.active}` } key={ `${tech.technology}` } >
                                    <KnowledgeApp tech={tech} />
                                </div>
                        );})}
                    </div>
                    <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Previous</span>
                    </button>
                    <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleIndicators" data-bs-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="visually-hidden">Next</span>
                    </button>
                </div>
            </div>
        </div>
    );
}
