import React from 'react';
import { useParallax } from '../../helpers/useParallax';

export const HomeScreen = (e) => {
	
	const [hatX, hatY] = useParallax(e, 100, 100);

	const [wallX, wallY] = useParallax(e, -700, 700);

	return (
		<figure 
			className="home__home-container w-100 h-100"
			style={{ 
				backgroundImage:'url(assets/screens/home.jpg)',
				transform: `translateX(${wallX}px) translateY(${wallY}px)` 
			}}
		>
			<main className="home__home-main w-100 h-100 d-flex align-items-center justify-content-evenly flex-row">
				<section className="w-50 h-100 d-sm-block d-none">
					<div className="h-50 w-100 fs-2">
						<div className="w-50 h-100 d-flex align-items-end justify-content-center flex-column animate__animated animate__slideInLeft animate__delay-1s">
							<h1>
								David
							</h1>
							<h3>
								Caro
							</h3>
						</div>							
					</div>
					<div className="w-100 h-50 d-flex align-items-center justify-content-end fs-2 animate__animated animate__slideInUp animate__delay-2s">
						<div className="justify-content-start">
							<h2>Portfolio</h2>
						</div>
					</div>
				</section>
				<section className="w-50 d-flex align-items-cente justify-content-center  animate__animated animate__fadeInUp">
					<img
						className="home__home-logo"
						src="assets/logo/logo.png"
						alt="logo" 
						style={{ transform: `translateX(${hatX}px) translateY(${hatY}px)` }}
					/>
				</section>
			</main>
		</figure>
	)
}
