import React from 'react'
import { useParallax } from '../../helpers/useParallax';

export const AboutScreen = (e) => {

	const [wallX, wallY] = useParallax(e, 1000, 1000);

	return (
		<figure 
		 className="about__about-container w-100 h-100 container" 
		 style={{
				backgroundImage:'url(assets/screens/about.jpg)',
				transform: `translateX(${wallX}px) translateY(${wallY}px)`
			}}
		>
			<main className="about__about-main w-100 h-100 row">
				<section className="col-xxl-3 col-12 h-100 d-flex align-items-center justify-content-center animate__animated animate__fadeInLeft">
					<img
					 alt="dabo" 
					 className="about__img" 
					 src="assets/img/dabo.png" 
					/>
				</section>
				<section className="col-xxl-9 col-12 h-100 d-flex flex-column align-items-center justify-content-center">
					<div className="row mt-3 animate__animated animate__fadeInDown">
						<h1 className="col-12 align-self-center">About Me</h1>
						<div className="col-12 about__title-line mb-3 animate__animated animate__zoomIn"/>
					</div>
					<div className="w-100 h-75 d-flex align-items-center justify-content-center animate__animated animate__fadeInUp">
						<div className="about__main-text w-100 h-100 d-flex align-items-center justify-content-center">
							<div className="about__text w-75">
								<p>
									Me llamo David Caro nací en Abril de 1998. 
									Actualmente vivo en Bahía Blanca, Buenos Aires.
										
									Empecé a estudiar de manera autodidacta
									programación en febrero del 2021 pero ya conocía
									algo de HTML y CSS.

									Tengo conocimientos de edición de video e imágenes
									música, todo aprendido de manera
									de autodidacta. Todo lo que se ve en el sitio web
									está hecho por mi mano.
									<br />
									Espero el sitio web sea de su agrado.
								</p>
							</div>	
						</div>
					</div>
				</section>
			</main>
		</figure>
	);
}
