import React from "react";
import { Link } from "react-router-dom";
import { animationSlide } from "../../helpers/animationSlide";
import { hideWork } from "../../helpers/hideWork";
import { workAnimation } from "../../helpers/workAnimation";
import { WorksApp } from "./WorksApp";

export const NavApp = () => {

	const handleAnimation = async (e) => {
		const target = e.target;
		const name = target.childNodes[1].name;
		const link = `#${name}`;

		animationSlide(link);
	}
	const handleHide = () =>{
		hideWork();
	}

	const handleWorkShow = () => {
		workAnimation();
	}
	return (
		<header className="nav__navbox-container d-flex justify-content-between align-items-center animate__animated animate__fadeIn animate__slow" >
			<hgroup className="nav__navbar-entry-bar h-100 d-flex justify-content-between align-items-center" >
				<div className="nav__navbar-solid-bar"/>
				<div className="nav__navbar-jobs-container h-100 w-100 d-flex align-items-center">
					<WorksApp />
					<div
						className="nav__navbar-morejobs d-flex align-items-center justify-content-between"
						onClick={ handleWorkShow }
						id="nav__button"
					>
						<div 
						 className="nav__navbar-jobs-triangle" 
						 onClick={handleHide}
						 />
						<h1 className="nav__navbar-jobs-title">
							More jobs
						</h1>
					</div>
				</div>
			</hgroup>
			<hgroup className="nav__navbar-container h-100 d-flex align-items-center justify-content-between" >
				<div className="list__box" >
					<li className="list__text" onMouseDown={ handleAnimation }>
						Home
						<Link
							name="home"
							id="home"
							to="/"
							style={{ display: "none" }}
						/>
					</li>
					<div className="list__line" />
				</div>
				<div className="list__box">
					<li className="list__text" onMouseDown={ handleAnimation }>
						About Me
						<Link
							name="about"
							to="/about"
							id="about"
							style={{ display: "none" }}
						/>
					</li>
					<div className="list__line" />
				</div>
				<div className="list__box">
					<li className="list__text" onMouseDown={ handleAnimation }>
						Skills
						<Link
							name="skills"
							to="/skills"
							id="skills"
							style={{ display: "none" }}
						/>
					</li>
					<div className="list__line" />
				</div>
				<div className="list__box">
					<li className="list__text" onMouseDown={ handleAnimation }>
						Contact
						<Link
							name="contact"
							to="/contact"
							id="contact"
							style={{ display: "none" }}
						/>
					</li>
					<div className="list__line" />
				</div>
			</hgroup>
		</header>
	);
}